/* Define CSS Variables */
:root {
    --primary-color: #4a69bd; /* Deep blue */
    --secondary-color: #ffcc00; /* Gold */
    --accent-color: #ff5733; /* Bright orange */
    --background-gradient: linear-gradient(135deg, #e9ecef 0%, #ffffff 100%);
    --text-color: #333; /* Dark text color */
    --white-color: #ffffff; /* White color */
    --light-gray: #f4f4f4; /* Light gray */
    --overlay-color: rgba(255, 204, 0, 0.1); /* Light yellow overlay */
    --font-family: 'Roboto', sans-serif;
    --border-radius: 10px; /* Border radius for rounded corners */
    --transition-speed: 0.3s; /* Transition speed */
}

/* General Styles */
body {
    font-family: var(--font-family);
    line-height: 1.6;
    margin: 0;
    padding: 0;
    background: var(--background-gradient);
    color: var(--text-color);
    overflow-x: hidden;
}

/* Header Styles */
header {
    background: var(--primary-color);
    color: var(--white-color);
    padding: 30px 0;
    text-align: center;
    position: relative;
    overflow: hidden; /* To contain the animated elements */
}

header h1 {
    animation: slideIn 1s ease-out;
    font-size: 2.5em;
}

/* Navigation Styles */
nav ul {
    list-style: none;
    padding: 0;
    margin: 10px 0;
}

nav ul li {
    display: inline;
    margin: 0 20px;
}

nav a {
    color: var(--white-color);
    text-decoration: none;
    transition: color var(--transition-speed), transform var(--transition-speed);
    position: relative; /* For pseudo-element */
}

nav a::after {
    content: '';
    display: block;
    width: 0%;
    height: 2px;
    background: var(--secondary-color);
    transition: width var(--transition-speed);
    position: absolute;
    left: 50%;
    bottom: -5px;
    transform: translateX(-50%);
}

nav a:hover {
    color: var(--secondary-color);
    transform: scale(1.1);
}

nav a:hover::after {
    width: 100%; /* Expand the line on hover */
}

/* Section Styles */
section {
    padding: 40px 20px;
    max-width: 800px;
    margin: 20px auto;
    background: var(--white-color);
    border-radius: var(--border-radius);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    animation: fadeIn 1s ease; /* Fade in animation for sections */
}

section::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--overlay-color);
    border-radius: var(--border-radius);
    z-index: 0;
    opacity: 0;
    transition: opacity 0.5s ease;
}

section:hover::before {
    opacity: 1;
}

section h2 {
    color: var(--primary-color);
    margin-top: 0;
    animation: bounceIn 0.8s ease; /* Bounce in effect for headings */
}

/* Project Styles */
.project {
    background: var(--light-gray);
    margin: 15px 0;
    padding: 15px;
    border-radius: var(--border-radius);
    transition: transform var(--transition-speed), box-shadow var(--transition-speed);
    position: relative; /* For animations */
    overflow: hidden; /* Contain children */
}

.project:hover {
    transform: scale(1.05) rotate(1deg); /* Slight rotation on hover */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.project h3 {
    position: relative; /* For hover effect */
}

.project h3::after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    background: var(--accent-color);
    transition: transform 0.3s ease;
    transform: scaleX(0); /* Start hidden */
}

.project:hover h3::after {
    transform: scaleX(1); /* Reveal on hover */
}

/* Contact Info Styles */
.contact-info {
    list-style: none;
    padding: 0;
}

.contact-info li {
    margin: 5px 0;
    transition: color var(--transition-speed);
}

.contact-info li:hover {
    color: var(--primary-color);
}

/* Footer Styles */
footer {
    text-align: center;
    padding: 15px 0;
    background: var(--primary-color);
    color: var(--white-color);
}

/* Animations */
@keyframes slideIn {
    0% {
        transform: translateY(-50px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes bounceIn {
    0% {
        transform: translateY(-30px);
        opacity: 0;
    }
    60% {
        transform: translateY(10px);
        opacity: 1;
    }
    100% {
        transform: translateY(0);
    }
}

/* Responsive Design */
@media (max-width: 600px) {
    nav ul li {
        display: block;
        margin: 10px 0;
    }

    section {
        padding: 30px 10px;
    }

    header h1 {
        font-size: 2em; /* Adjust header size for small screens */
    }
}
